import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import JobOfferCandidatesView from 'Employer/views/JobOfferCandidatesView/JobOfferCandidatesView'
import { getJobOfferById } from 'Shared/selectors/jobOffers'
import { fetchJobOfferByIdIfNeeded } from 'Shared/ducks/jobOffers'
import withAlgolia from 'Algolia/hoc/withAlgolia'
import INDEX_NAME, { INFINITE_SEARCH_ITEMS_PER_PAGE_COUNT } from 'Shared/constants/Algolia'
import { fetchCountriesIfNeeded, fetchLanguagesIfNeeded } from 'Shared/ducks/resources'
import { getUrlToSearchState, getSearchStateValues } from 'Shared/selectors/url'
import { hasDatabaseSearchAvailable } from 'Shared/selectors/membership'
import withScripts from 'TrackingScripts'
import { getSavedSearchById } from 'Shared/selectors/savedSearchCriteria'
import { fetchCurrentLocationIfNeeded } from 'Shared/ducks/geolocation'
import { getContactedCandidatesIds, getViewedCandidatesIds } from 'Shared/selectors/candidateSearchFilters'
import { setViewedFilterValue, setContactedFilterValue } from 'Shared/ducks/candidateSearchFilters'
import { userEmail } from 'Shared/selectors/user'
import { isAutoApprovalEnabled } from 'Shared/selectors/geolocation'

const configureProps = { hitsPerPage: INFINITE_SEARCH_ITEMS_PER_PAGE_COUNT }

const mapStateToProps = (state, ownProps) => {
  const { jobOfferId } = ownProps.match.params
  const jobOffer = getJobOfferById(state, jobOfferId)

  const searchState = getUrlToSearchState(ownProps.location.search, true)
  const canUseDatabaseSearch = hasDatabaseSearchAvailable(state)

  return ({
    selectedSearch: getSavedSearchById(state, ownProps.match.params.savedSearchId),
    jobOffer,
    indexName: searchState.sort || INDEX_NAME.CANDIDATES,
    searchState,
    languages: state.resources.languages.entries,
    countries: state.resources.countries.entries,
    canUseDatabaseSearch,
    configureProps,
    searchStateValues: getSearchStateValues(searchState),
    currentLocation: state.geolocation.currentLocation,
    viewedCandidatesIds: getViewedCandidatesIds(state),
    contactedCandidatesIds: getContactedCandidatesIds(state),
    viewedFilterValue: state.candidateSearchFilters.viewedFilterValue,
    contactedFilterValue: state.candidateSearchFilters.contactedFilterValue,
    userEmail: userEmail(state),
    isAutoApprovalEnabled: isAutoApprovalEnabled(state)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchJobOffer: () => dispatch(fetchJobOfferByIdIfNeeded(ownProps.match.params.jobOfferId)),
  fetchCountries: () => dispatch(fetchCountriesIfNeeded()),
  fetchLanguages: () => dispatch(fetchLanguagesIfNeeded()),
  fetchCurrentLocationIfNeeded: () => dispatch(fetchCurrentLocationIfNeeded()),
  setViewedFilterValue: value => dispatch(setViewedFilterValue(value)),
  setContactedFilterValue: value => dispatch(setContactedFilterValue(value))
})

export default withScripts(withRouter(connect(mapStateToProps, mapDispatchToProps)(withAlgolia(JobOfferCandidatesView))))



