import React from 'react'
import cx from 'classnames'
import { t } from 'i18next'
import PropTypes from 'prop-types'
import moment from 'moment'
import URLSearchParams from 'url-search-params'
import Container from 'Shared/components/Container'
import BackButton from 'Shared/components/BackButton/BackButton'
import { getCountryNameByISOCode } from 'Shared/helpers/Country'
import CandidatesViewGridConnector from 'Employer/connectors/Views/Candidates/CandidatesViewGridConnector'
import { calculateAgeByYears } from 'Shared/helpers/date'
import { SearchResults } from 'Algolia/components/SearchResult'
import INDEX_NAME from 'Shared/constants/Algolia'
import DropdownMenu from 'Shared/components/DropdownMenu'
import DropdownArrow from 'Shared/components/DropdownArrow'
import CandidatesStarRatingWrapper from 'Employer/components/CandidatesStarRatingWrapper'
import CandidatesBulkManagerConnector from 'Employer/connectors/BulkManager/CandidatesBulkManagerConnector'
import { MIN_CANDIDATES_RATING_VALUE } from 'Shared/constants/Candidates'
import { searchStateToUrl, urlToSearchState } from 'Shared/helpers/querySearch'
import { isCandidatesDefaultSearch } from 'Employer/utils/isCandidatesDefaultSearch'
import { candidateViewedFiltersNames, candidateContactedFiltersNames } from 'Shared/constants/candidatesFilters'
import { transformLabels, handleCandidateIdFilters } from 'Shared/helpers/Algolia'
import CandidatesSearchBoxConnector from 'Employer/connectors/Components/CandidatesSearchBoxConnector'

import './JobOfferCandidatesView.scss'
import { getCandidateSearchCriteria } from '../../utils/getCandidateSearchCriteria'
import BuyCreditsBanner from 'Employer/connectors/BuyCreditsBanner'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'

const R = require('ramda')

const SortOptions = [INDEX_NAME.CANDIDATES, INDEX_NAME.CANDIDATES_LAST_LOGGED_IN]

class JobOfferCandidatesView extends React.PureComponent {
  state = {
    isDropdownOpen: false,
    selectedSortOption: this.props.indexName,
    isSavedSearchLoaded: false,
    viewedFilterValue: candidateViewedFiltersNames().all,
    contactedFilterValue: candidateContactedFiltersNames().all
  }

  now = parseInt(moment().format('YYYYMMDD'), 10)

  async componentDidMount () {
    this.props.fetchLanguages()
    this.props.fetchJobOffer()
    await this.props.fetchCountries()
    await this.props.fetchCurrentLocationIfNeeded()

    const candidateCountries = R.pathOr([], ['currentLocation', 'candidateCountries'], this.props)
    const currentSearch = urlToSearchState(this.props.location.search)
    candidateCountries.length &&
    (R.isNil(currentSearch.refinementList) || R.isEmpty(currentSearch.refinementList) || !R.path(['refinementList', 'countryOfResidence'], currentSearch).length) &&
      this.props.history.replace({
        pathname: this.props.history.location.pathname,
        search: searchStateToUrl({
          ...currentSearch,
          refinementList: {
            countryOfResidence: candidateCountries.map(key => R.path(['countries', key, 'country'], this.props))
          }
        })
      })

    if (!R.isNil(this.props.jobOffer)) {
      this.props.addFilter({ departments: `departments:${this.props.jobOffer.departmentId}` }, true)
    }
  }

  hasQueryChanged (nextProps) {
    const currentSearchQuery = R.pipe(
      R.propOr(false, 'query'),
      JSON.parse
    )(nextProps.selectedSearch)

    const nextSearchquery = urlToSearchState(nextProps.location.search, true)

    return R.not(R.equals(currentSearchQuery, {...nextSearchquery, configure: {...R.dissoc('optionalFilters', nextSearchquery.configure)}}))
  }

  componentWillReceiveProps (nextProps) {
    if (nextProps.selectedSearch.id && this.hasQueryChanged(nextProps)) {
      this.props.history.push({
        pathname: `/employer/job-offer/${this.props.match.params.jobOfferId}/candidates`,
        search: nextProps.location.search
      })
    }

    if (!nextProps.match.params.savedSearchId || nextProps.match.params.savedSearchId !== this.props.match.params.savedSearchId) {
      this.setState({ isSavedSearchLoaded: false })
    }

    if (R.not(R.isEmpty(nextProps.searchStateValues)) && !isCandidatesDefaultSearch(nextProps.searchStateValues) && !nextProps.match.params.savedSearchId) {
      const fireEvent = payload => {
        this.props.manualSegmentScripts.searchForCandidates(this.props.employerId, { ...payload, ...this.props.match.params }, this.props.userEmail)
      }

      R.pipe(
        getCandidateSearchCriteria,
        fireEvent
      )(nextProps.searchState)
    }
  }

  componentDidUpdate (prevProps) {
    if (R.isNil(prevProps.jobOffer) && !R.isNil(this.props.jobOffer)) {
      this.props.addFilter({ departments: `departments:${this.props.jobOffer.departmentId}` }, true)
    }

    if (this.props.match.params.savedSearchId && this.props.match.params.savedSearchId !== prevProps.match.params.savedSearchId) {
      this.setState({ isSavedSearchLoaded: true }, () => setTimeout(() => this.setState({ isSavedSearchLoaded: false }), 1500))
    }

    if (this.props.viewedFilterValue !== prevProps.viewedFilterValue || this.props.contactedFilterValue !== prevProps.contactedFilterValue) {
      this.handleFilterHitsByIds()
    }
  }

  isFilterLoaded = filterName => this.state.isSavedSearchLoaded && R.has(filterName)(this.props.searchStateValues)

  translateLanguage = label => R.pathOr('', [label.toUpperCase(), 'language'], this.props.languages)

  translateNationality = label => R.pathOr('', [label, 'nationality'], this.props.countries)

  handleFilterHitsByIds = () => {
    const { viewedFilterValue, contactedFilterValue, viewedCandidatesIds, contactedCandidatesIds, addObjectIdFilters } = this.props
    handleCandidateIdFilters(viewedFilterValue, contactedFilterValue, viewedCandidatesIds, contactedCandidatesIds, addObjectIdFilters)
  }

  transformLabelsConfig = {
    'driversLicence': item => {
      return {
        ...item,
        items: item.items.map(el =>
          ({ label: t('common.drivingLicenseWithValue', { value: el.label }), value: el.value })
        )}
    },
    'yearOfBirth': item => {
      return {
        ...item,
        items: item.items.map(el => (
          { label: t('common.ageWithNumber', { age: calculateAgeByYears(el.label) }), value: el.value })
        )}
    },
    'languages': item => {
      return {
        ...item,
        items: item.items.map(el => (
          { label: this.translateLanguage(el.label), value: el.value })
        )}
    },
    'nationality': item => {
      return {
        ...item,
        items: item.items.map(el => (
          { label: this.translateNationality(el.label), value: el.value })
        )}
    }
  }

  sortOptionActionsMap = {
    [INDEX_NAME.CANDIDATES]: () => {
      this.updateQuerySearch('sort', INDEX_NAME.CANDIDATES)
      return this.props.changeIndex(INDEX_NAME.CANDIDATES)
    },
    [INDEX_NAME.CANDIDATES_LAST_LOGGED_IN]: () => {
      this.updateQuerySearch('sort', INDEX_NAME.CANDIDATES_LAST_LOGGED_IN)
      return this.props.changeIndex(INDEX_NAME.CANDIDATES_LAST_LOGGED_IN)
    }
  }

  updateQuerySearch = (key, value) => {
    const { history } = this.props
    const query = new URLSearchParams(history.location.search)

    query.set(key, value)

    history.replace({...history.location, search: query.toString()})
  }

  transformedLabels = (refinedItems) => transformLabels(refinedItems, ['hasVideoCv', 'rating'], this.transformLabelsConfig)

  toggleArrow = isDropdownOpen => this.setState({ isDropdownOpen })

  closeDropdown = () => this.setState({ isDropdownOpen: false })

  get triggerButton () {
    return (
      <div className='job-offer-candidates-view__sort-trigger'>
        <span>{t('common.sortedBy')}</span>
        <span>{t(`candidatesList.sortDropdown.${this.state.selectedSortOption}`)}</span>
        <DropdownArrow isOpen={this.state.isDropdownOpen} />
      </div>
    )
  }

  setSelectedOption = selectedSortOption => () => {
    this.sortOptionActionsMap[selectedSortOption]()
    return this.setState({ selectedSortOption: selectedSortOption })
  }

  get sortMenuItems () {
    return SortOptions.map((item, index) => {
      const itemClassname = cx({
        'job-offer-candidates-sort-item': true,
        'job-offer-candidates-sort-item--selected': this.state.selectedSortOption === item
      })

      return (
        <li
          className={itemClassname}
          key={`${index}-${item}`}
          onClick={this.setSelectedOption(item)}
        >
          {t(`candidatesList.sortDropdown.${item}`)}
        </li>
      )
    })
  }

  isRatingLoaded = () => {
    return this.isFilterLoaded('rating') && R.path(['rating', 'min'], this.props.searchStateValues) > 0
  }

  showStarRatingFilterIfNeeded = () => !this.props.isAutoApprovalEnabled && (
    <CandidatesStarRatingWrapper
      min={0}
      max={5}
      attribute='rating'
      shouldUpdate={R.isEmpty(this.props.searchState)}
      ratingValue={parseInt(R.pathOr(
        MIN_CANDIDATES_RATING_VALUE,
        ['props', 'searchState', 'range', 'rating', 'min'],
        this
      ), 10)}
      isLoaded={this.isRatingLoaded()}
    />
  )

  render () {
    const { jobOffer, canUseDatabaseSearch } = this.props

    return R.not(R.isNil(jobOffer)) && R.not(R.isEmpty(jobOffer)) && (
      <div>
        <CandidatesSearchBoxConnector />
        <Container className='job-offer-candidates__container parsing--hidden'>
          <PageTopHeaderMobile title={t('common.candidates')} />
          {!canUseDatabaseSearch && <BuyCreditsBanner />}
          <div className='job-offer-candidates-view__header'>
            <BackButton
              link={'/employer/job-offers/active'}
              name={t('common.goBackToMyJobs')}
            />
            <div className='job-offer-candidates-view__header__title'>{jobOffer.title}</div>
            <div>
              <span className='job-offer-candidates-view__header__details'>
                { t(`employer.jobOffers.jobTypes.${jobOffer.jobType}`) }
              </span>
              <span className='job-offer-candidates-view__header__details'>
                { t(`employer.jobOffers.commitmentTypes.${jobOffer.commitment}`) }
              </span>
              <span className='job-offer-candidates-view__header__details'>
                {jobOffer.city}, {getCountryNameByISOCode(jobOffer.country)}
              </span>
              {jobOffer.isPrivate && <i className='job-offer-candidates-view__header__is-private ion-ios-eye-off' />}
            </div>
          </div>
          <div>
            <div className='columns job-offer-candidates-view__search-results'>
              <div className='column col-6 col-sm-12'>
                <SearchResults
                  transformItems={this.transformedLabels}
                  isDefaultSearch={isCandidatesDefaultSearch(this.props.searchStateValues) &&
                  this.props.viewedFilterValue === candidateViewedFiltersNames().all &&
                  this.props.contactedFilterValue === candidateContactedFiltersNames().all}
                  selectedSearchName={this.props.selectedSearch.name}
                  viewedFilterValue={this.props.viewedFilterValue}
                  viewedFilterOnChange={this.props.setViewedFilterValue}
                  contactedFilterValue={this.props.contactedFilterValue}
                  contactedFilterOnChange={this.props.setContactedFilterValue}
                  hideCurrentRefinements
                />
              </div>
              <div className='column col-6 col-sm-12 job-offer-candidates-view__additional-filters'>
                { this.showStarRatingFilterIfNeeded() }
                <DropdownMenu
                  onToggle={this.toggleArrow}
                  onClose={this.closeDropdown}
                  triggerButton={this.triggerButton}
                  align={'right'}
                  closeOnOutsideClick
                >
                  {this.sortMenuItems}
                </DropdownMenu>
              </div>
            </div>
            <CandidatesViewGridConnector viewedFilterValue={this.state.viewedFilterValue} />
            <CandidatesBulkManagerConnector />
          </div>
        </Container>
      </div>
    )
  }
}

JobOfferCandidatesView.propTypes = {
  selectedSearch: PropTypes.object
}

export default JobOfferCandidatesView



